import {useEffect} from "react";

export function useTitle({openFood, orders}) {
  useEffect(() => {
    if (openFood) {
      document.title = openFood.name;
    } else {
      document.title = orders.length === 0 ? `What's for dinner?` : `[${orders.length}] item${orders.length === 1 ? "" : 's'} in your order!`;
    }
  }, [openFood, orders]);
}